.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* { margin:0; 
  padding:0; 
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

body {


  font-weight: 500;
  color: #282828; 
  background: #ffffff;
  -webkit-text-size-adjust: 100%; /* fix for ios */
  height: auto;
  min-height: 100%;
  margin: 0;

}

header {
  
  width: 100%;
  background-color: #007cc7;
  position: fixed;
  display: table;
  /*display: flex;*/
  /*flex-direction: row; */
  /*justify-content: center;*/
  /*align-items: center;*/
  /*text-align: center;*/
  color: #ffffff;
  top:0;
  left:0;
  z-index: 101;
  height: 60px;
  -webkit-box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.12);
          box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.12);

}

header h2{
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}

main{

  margin-top: 90px;
  width:  100%;
  margin-bottom: 40px;
}

section{
  width: 90%;
  margin: 0 auto;
}


